            <ion-toolbar style="padding: 10px;">
                <ion-title style="text-transform: uppercase;" class="his-lg-text">NLIMS Order</ion-title>
            </ion-toolbar>
        </ion-header>
        <ion-content>
            <ion-grid>
                <ion-row>
                    <ion-col size="6">
                        <ion-card>
                            <ion-toolbar color="primary">
                                <ion-title>Order Details</ion-title>
                            </ion-toolbar>
                            <ion-item>
                                <ion-label style="font-size: 20px; font-weight: 600">Test Type</ion-label>
                                <ion-label style="font-size: 18px;">
                                    {{ details?.tests.map((test) => test.test_type).join(', ') }}
                                </ion-label>
                            </ion-item>
                            <ion-item>
                                <ion-label style="font-size: 20px; font-weight: 600">Date Ordered</ion-label>
                                <ion-label slot="end" style="font-size: 18px;">{{
                                    moment(details?.created_date).format(DATE_FORMAT)
                                    }}</ion-label>
                            </ion-item>
                            <ion-item>
                                <ion-label style="font-size: 20px; font-weight: 600">Priority</ion-label>
                                <ion-label slot="end" style="font-size: 18px;">{{ details?.priority }}</ion-label>
                            </ion-item>
                            <ion-item>
                                <ion-label style="font-size: 20px; font-weight: 600">Test Status</ion-label>
                                <ion-label style="font-size: 18px;">
                                    {{ details?.tests.map((test) => test.test_status).join(', ') }}
                                </ion-label>
                            </ion-item>
                            <ion-item>
                                <ion-label style="font-size: 20px; font-weight: 600">Request Origin</ion-label>
                                <ion-label slot="end" style="font-size: 18px;">{{ details?.sending_facility
                                    }}</ion-label>
                            </ion-item>
                            <ion-item>
                                <ion-label style="font-size: 20px; font-weight: 600">Requesting
                                    Ward/Location</ion-label>
                                <ion-label slot="end" style="font-size: 18px;">{{ details?.facility_section
                                    }}</ion-label>
                            </ion-item>
                            <ion-item>
                                <ion-label style="font-size: 20px; font-weight: 600">Receiving Facility</ion-label>
                                <ion-label slot="end" style="font-size: 18px;">{{ details?.receiving_facility
                                    }}</ion-label>
                            </ion-item>
                            <ion-item>
                                <ion-label style="font-size: 20px; font-weight: 600">Requesting Physician</ion-label>
                                <ion-label slot="end" style="font-size: 18px;">{{ details?.requested_by }}</ion-label>
                            </ion-item>
                            <ion-item>
                                <ion-label style="font-size: 20px; font-weight: 600">Registered By</ion-label>
                                <ion-label slot="end" style="font-size: 18px;">{{ details?.collected_by }}</ion-label>
                            </ion-item>
                        </ion-card>
                    </ion-col>
                    <ion-col size="6">
                        <ion-card>
                            <ion-toolbar color="primary">
                                <ion-title> Patient Details </ion-title>
                            </ion-toolbar>
                            <ion-list>
                                <ion-item>
                                    <ion-label style="font-size: 20px; font-weight: 600">NPID</ion-label>
                                    <ion-label slot="end" style="font-size: 18px;">{{ details?.patient_identifiers?.npid
                                        }}</ion-label>
                                </ion-item>
                                <ion-item>
                                    <ion-label style="font-size: 20px; font-weight: 600">ART Regimen</ion-label>
                                    <ion-label slot="end" style="font-size: 18px;">{{
                                        details?.patient_identifiers?.art_regimen }}</ion-label>
                                </ion-item>
                                <ion-item>
                                    <ion-label style="font-size: 20px; font-weight: 600">ARV Number</ion-label>
                                    <ion-label slot="end" style="font-size: 18px;">{{
                                        details?.patient_identifiers?.arv_number }}</ion-label>
                                </ion-item>
