                                </ion-item>
                                <ion-item>
                                    <ion-label style="font-size: 20px; font-weight: 600">Tracking Number</ion-label>
                                    <ion-label slot="end" style="font-size: 18px;">{{
                                        details?.tracking_number
                                        }}</ion-label>
                                </ion-item>
                                <ion-item>
                                    <ion-label style="font-size: 20px; font-weight: 600">Status</ion-label>
