                        </ion-card>

                        <ion-card>
                            <ion-toolbar color="primary">
                                <ion-title> Results </ion-title>
                            </ion-toolbar>
                            <ion-list :inset="false">
                                <ion-item v-for="result in details?.results" :key="result">
                                    <ion-label style="font-size: 20px; font-weight: 600">{{
                                        Object.keys(result)[0]}}</ion-label>
                                    <ion-label slot="end" style="font-size: 18px;">{{ Object.values(result)[0]
                                        }}</ion-label>
