                    <ion-icon :icon="arrowBack" slot="icon-only" style="margin-right: 5px;"></ion-icon>
                    Back
                </ion-button>

                <ion-button @click="mergeOrder" size="large" slot="end" color="success">
                    <ion-icon :icon="add" slot="icon-only" style="margin-right: 5px;"></ion-icon>
                    Merge
                </ion-button>
                <ion-button @click="printTrackingNumber" size="large" slot="end">
                    <ion-icon :icon="print" slot="icon-only" style="margin-right: 5px;"></ion-icon>
                    Print Tracking Number
                </ion-button>
            </ion-toolbar>
